.example{
  background-color: @light-gray;
  padding: 25px 20px 40px 20px ;
  @media (min-width:@tablet-width){
    background-color: transparent;
    padding: 0;
    margin-bottom: 59px;
    padding-left: 30px;
    padding-right: 30px;
  }
  @media (min-width:@desktop-width){
    background-color: @gray;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.example__wrapper{
  margin: 0 auto;
  @media (min-width:@desktop-width){
    width: 1220px;
  }
}
.example__layout{
  @media (min-width:@desktop-width){
    display: flex;
  }
}
.example__info{
  @media (min-width:@tablet-width){
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  @media (min-width:@desktop-width){
    width: 610px;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
  }
}
.example__caption{
  margin-top: 0;
  margin-bottom: 40px;
  @media (min-width:@desktop-width){
    margin-top: -140px;
    margin-bottom: 146px;
  }
}
.example__description{
line-height: 18px;
margin-bottom: 34px;
@media (min-width:@tablet-width){
  font-size: 16px;
line-height: 24px;
min-width: 700px;
margin-bottom: 69px;
}
@media (min-width:@desktop-width){
  width: 436px;
}
}
.example__item{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.example__list{
  display: grid;
  grid-template-columns: 124px 124px;
  column-gap: 32px;
  list-style-type: none;
  padding: 0;
  margin-top: 0;
  text-align: center;
  margin-bottom: 24px;
  @media (min-width:@tablet-width){
  grid-template-columns: 162px 162px;
  column-gap: 42px;
  margin-bottom: 0;
  }
  @media (min-width:@desktop-width){
    margin-bottom: 47px;
  }
}
.example__num{
font-family: Oswald;
font-style: normal;
font-weight: normal;
font-size: 24px;
line-height: 24px;
position: relative;
text-transform: uppercase;
width: 124px;
height: 56px;
border: 1px solid #CDCDCD;
box-sizing: border-box;
border-radius: 3px;
display: flex;
align-items: center;
justify-content: center;
@media (min-width:@tablet-width){
  font-size: 30px;
line-height: 37px;
width: 162px;
height: 72px;
}
}

.example__container{
  @media (min-width:@tablet-width){
    margin: 0 auto;
    margin-top: 48px;
    background-color: @light-gray;
    padding-bottom: 61px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  @media (min-width:@desktop-width){
    background-color: transparent;
    width: 610px;
  }
}
.example__text{
  font-size:12px;
  line-height: 100%;
  color: @dark-gray;
  width: 87px;
  text-align: center;
  background: @light-gray;
  z-index: 2;
  position: relative;
  top: -10px;
  left: 0;
  @media (min-width:@tablet-width){
    br{display: none;}
    font-size: 14px;
line-height: 14px;
width: max-content;
background: @white;
padding-left: 9px;
padding-right: 9px;
  }
  @media (min-width:@desktop-width){
    background-color: @gray;
  }
}
.example__cost{
  font-family: Oswald;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 20px;
text-transform: uppercase;
display: flex;
justify-content: center;
margin-bottom: 40px;
@media (min-width:@tablet-width){
font-size: 20px;
line-height: 20px;
display: flex;
flex-direction: column;
align-items: flex-end;
}
@media (min-width:@desktop-width){
flex-direction: row;
align-items: flex-start;
}
}
.example__cost-text{
margin: 0;
}
.example__cost-text:nth-child(1){
  @media (min-width:@tablet-width){
    margin-bottom: 9px;
  }
  @media (min-width:@desktop-width){
    margin-right: 63px;
  }
}
.example__range{
  margin-top: 15px;
}
.example__button{
border: none;
background-color: transparent;
font-family: Oswald;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 20px;
text-transform: uppercase;
color: @black;
@media (min-width:@tablet-width){
  font-size: 20px;
line-height: 30px;
}
}
